@riseColor : #F5222D;
@declineColor : #03AD8F;
@commonYellow : #F9CA41;
@commonBlue : #4096FF;

// 
.noBorderButton {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

// 
.scrollbarCommonStyle {
  &::-webkit-scrollbar {
    width: 6px;
    height: 1px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background: #ccc;
  }

  &::-webkit-scrollbar-track {
    background: #fff;
  }
}

// modal
.commonModalContent(@modalMinHeight){
  min-height: @modalMinHeight;
  max-height: calc(100vh - 180px);
  overflow-y: scroll;
  .scrollbarCommonStyle;
  padding: 24px;
  padding-top: 0;
}

// custom modal header sticky to modal top
.modalHeaderSticky {
  font-size: 16px;
  font-weight: bold;
  color: #333;

  button {
    float: right;
  }

  position : sticky;
  top : 0;
  z-index : 100;
  background-color: white;
  padding : 24px 0 20px 0;
}

// 
.descriptionLabel {
  color: #666;
  font-size: 12px;
}

//model 
.modelClosed{
  :global {
    .ant-btn{
      padding:0;
      height: 20px;
      line-height: 7px;
      > img {
        padding-right: 1px;
        margin: 0;
        }
    }
  }
}

// 
.overflowEllipsis (@width) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
  max-width: @width;
}

// for form item that cannot set required rule
.formItemRequired {
  :global {
    .ant-form-item-label {
      label::before {
        display: inline-block;
        margin-right: 4px;
        color: #ff4d4f;
        font-size: 14px;
        font-family: SimSun, sans-serif;
        line-height: 1;
        content: '*';
      }
    }
  }
}